import React from "react";
import { useNavigate, useLocation } from 'react-router-dom';
import Navigation from "../components/navigation";
import Footer from "../components/footer"

export default function PageDone() {
    // Fungsi untuk mendapatkan objek history
    const history = useNavigate();
    const location = useLocation();

    // Menampilkan pesan ke pengguna
    const handleBackToShop = () => {
        history('/');
    };
    const handleBackToCheckout = () => {
        history('/page-checkout');
    };


    const isSuccess = new URLSearchParams(location.search).get('success') === 'AS21321asdas123assd2423ea';
    const isError = new URLSearchParams(location.search).get('error') === 'AS21321asdasI23assd2423ea';

    return (
        <>
            {(isSuccess || isError) && (
                <Navigation />
            )}
            <section className="done">
                {isSuccess && (
                    <>
                        <div className="done-success">
                            <div className="image">
                                <img src="assets/image/bg-hero.png" alt="success" />
                            </div>
                            <div className="message">
                                <div className="icon">
                                    <i className="fa-solid fa-check"></i>
                                </div>
                                <h2>Successfully ordered</h2>
                                <p>Order #324152312567 has been placed.</p>
                                <p>After sending your order, we will send a letter with the TTH number.</p>
                            </div>
                        </div>
                        <div className="button" onClick={handleBackToShop}>
                            <button className="btn-a">Back to Home</button>
                        </div>
                    </>
                )}
                {isError && (
                    <>
                        <div className="done-error">
                            <div className="image">
                                <img src="assets/image/bg-hero.png" alt="error" />
                            </div>
                            <div className="message">
                                <div className="icon">
                                    <i className="fa-solid fa-times"></i> {/* Use a different icon for error */}
                                </div>
                                <h2>Checkout Failed</h2>
                                <p>An error occurred while processing your order. Please try again.</p>
                            </div>
                        </div>
                        <div className="button" onClick={handleBackToCheckout}>
                            <button className="btn-a">Back to Checkout</button>
                        </div>
                    </>
                )}
                {!isError && !isSuccess && (
                    <>
                        <div className="done-error">
                            <div className="image">
                                <img src="assets/image/bg-hero.png" alt="error" />
                            </div>
                            <div className="message">
                                <div className="icon">
                                    <i className="fa-solid fa-times"></i> {/* Use a different icon for error */}
                                </div>
                                <h2>You are wrong?</h2>
                                <h2>ERROR: 404</h2>
                                <p>An error occurred while processing this page. Please try again.</p>
                            </div>
                        </div>
                        <div className="button" onClick={handleBackToShop}>
                            <button className="btn-a">Back to Home</button>
                        </div>
                    </>
                )}
            </section>
            {(isSuccess || isError) && (
                <Footer />
            )}
        </>
    );
};