import React from 'react';
import { useLocation } from 'react-router-dom';
import productData from '../data/productsData';
import Navigation from '../components/navigation';
import ProductsPagination from '../components/product-pagination';


const PageSearch = () => {
    const location = useLocation();
    const searchTerm = location.state?.searchTerm; // Optional chaining to handle undefined state
    
    return (
        <>
            <Navigation searchTerm={searchTerm} />
            <ProductsPagination productsData={productData} itemsPerPage={20} searchTerm={searchTerm} />
        </>
    );
};

export default PageSearch;