import * as React from 'react';

export const useCartAndWishlist = () => {
  
  const [wishlist, setWishlist] = React.useState([]);
  React.useEffect(() => {
    const storedWishlist = localStorage.getItem('wishlist');
    if (storedWishlist) {
      const wishlistProducts = JSON.parse(storedWishlist);
      setWishlist(wishlistProducts);
    }
  }, []);

  React.useEffect(() => {
    localStorage.setItem('wishlist', JSON.stringify(wishlist));
  }, [wishlist]);
  

  
  const [cartProducts, setCartProducts] = React.useState([]);
  React.useEffect(() => {
    const cartsJSON = localStorage.getItem("carts");
    if (cartsJSON) {
      const carts = JSON.parse(cartsJSON);
      setCartProducts(carts);
    }
  }, []);

  React.useEffect(() => {
    localStorage.setItem("carts", JSON.stringify(cartProducts));
  }, [cartProducts]);

  return { cartProducts, setCartProducts, wishlist, setWishlist };
};
