import * as React from "react";
import { useLocation } from "react-router-dom";
import { useCartAndWishlist } from "../controllers/cartAndWishlist";
import Paginate from "react-paginate";
import Placeholder from "./placeholder";
import Card from "./product-card";

const ProductsPagination = ({ productsData, itemsPerPage, searchTerm }) => {
    // Menyimpan Data Carts dan Wishlist Products
    const { cartProducts, setCartProducts, wishlist, setWishlist } = useCartAndWishlist();

    // Mengelola Alert
    const [showSuccessAlert, setShowSuccessAlert] = React.useState(null);
    const alertTimeoutRef = React.useRef(null);
    // Menerima ID produk dan menemukan produk yang sesuai, kemudian menyimpannya dalam state carts.
    const handleAddToCart = (productId) => {
        const chosenProduct = productsData.find((product) => product.id === productId);

        // Periksa apakah produk sudah ada di keranjang belanja
        const isProductInCart = cartProducts.some((product) => product.id === chosenProduct.id);

        if (isProductInCart) {
            // Jika produk sudah ada di keranjang belanja, update jumlah produk
            const updatedCartProducts = cartProducts.map((product) => {
                if (product.id === chosenProduct.id) {
                    return { ...product, quantity: product.quantity + 1 };
                }
                return product;
            });
            setCartProducts(updatedCartProducts);
            localStorage.setItem("carts", JSON.stringify(updatedCartProducts));
        } else {
            // Jika produk belum ada di keranjang belanja, tambahkan produk baru
            const updatedCartProducts = [...cartProducts, { ...chosenProduct, quantity: 1 }];
            setCartProducts(updatedCartProducts);
            localStorage.setItem("carts", JSON.stringify(updatedCartProducts));
        }

        // Clear any existing timeout before setting a new one
        clearTimeout(alertTimeoutRef.current);
        // Menampilkan pesan ke pengguna
        // Update state to show alert
        setShowSuccessAlert(productId); // Set product ID for alert display

        // ... (Optional) Delay to simulate alert duration
        alertTimeoutRef.current = setTimeout(() => setShowSuccessAlert(false), 3000); // Hide after 3 seconds
    };
    // Menerima ID produk dan menemukan produk wishlist yang sesuai, kemudian menyimpannya dalam state wishlist.
    const handleAddToWishlist = (productId) => {
        if (!wishlist.includes(productId)) {
            setWishlist([...wishlist, productId]);
            localStorage.setItem('wishlist', JSON.stringify([...wishlist, productId]));
        } else {
            const updatedWishlist = wishlist.filter((id) => id !== productId);
            setWishlist(updatedWishlist);
            localStorage.setItem('wishlist', JSON.stringify(updatedWishlist));
        }
    };



    // Mengelola State dari Navigation
    const { state } = useLocation();
    const title = state ? state.title : "Best Products";
    // Mengelola Filter Products Search & State
    const filteredProducts = productsData.filter((product) => {
        // Adjust the filtering condition based on your needs
        if (title === "Best Products") {
            return product.sold >= 1000;
        } else if (title === "Candy") {
            const typesToInclude = ['Candy']; // Replace with your desired types
            return typesToInclude.includes(product.type[1]);
        } else if (title === "Chocolate Bars") {
            const typesToInclude = ['Chocolate Bars'];
            return typesToInclude.includes(product.type[1]);
        } else if (title === "Diet Sweets") {
            const typesToInclude = ['Diet Sweets'];
            return typesToInclude.includes(product.type[1]);
        } else if (title === "Gift Sweets") {
            const typesToInclude = ['Gift Sweets'];
            return typesToInclude.includes(product.type[1]);
        } else if (searchTerm) { // Penyaringan berdasarkan search term
            const searchTermLower = searchTerm.toLowerCase();
            return product.name.toLowerCase().includes(searchTermLower) || product.type.join(' ').toLowerCase().includes(searchTermLower);
        } else {
            return productsData; // Mengembalikan semua produk jika tidak ada search term
        }
    });
    // Mengelola Page
    const [currentPage, setCurrentPage] = React.useState(1);
    // Menghitung Page
    const pageCount = Math.ceil(filteredProducts.length / itemsPerPage);
    // Menghitung products yang tampil
    const productsToDisplay = filteredProducts.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );
    // Mengelola Perubahan Page
    const handlePageChange = (data) => {
        setCurrentPage(data.selected + 1);
    };



    // Mengelola Loading Card
    const [loading, setLoading] = React.useState(true);
    // Mengatur lama Waktu Placeholder
    setTimeout(() => {
        setLoading(false);
    }, 2000);

    return (
        <>
            <section className="products container">
                <h1>{(!title) ?
                    (searchTerm === "") ?
                        "Search: All Products" + searchTerm : "Search: " + searchTerm
                    : title}</h1>
                <div className="cards-group">
                    {loading ? (
                        <Placeholder />
                    ) : (
                        productsToDisplay.map((product) => (
                            <Card
                                key={product.id}
                                product={product}
                                handleAddToWishlist={handleAddToWishlist}
                                handleAddToCart={handleAddToCart}
                                wishlist={wishlist}
                                showSuccessAlert={showSuccessAlert === product.id}
                            />
                        ))
                    )}
                </div>
                <Paginate
                    pageCount={pageCount}
                    marginPagesDisplayed={2}
                    pageRangeDisplayed={5}
                    onPageChange={handlePageChange}
                    containerClassName="pagination"
                    activeClassName="active"
                />
            </section>
        </>
    );
};

export default ProductsPagination;
