import React from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import PageHome from './pages/page-home';
import PageShop from './pages/page-shop';
import PageCarts from './pages/page-carts';
import PageWishLisht from './pages/page-wishlist';
import PageCheckout from './pages/page-checkout';
import PageSearch from './pages/page-search';
import PageDetailProduct from './pages/page-detail-product';
import PageDone from './pages/page-done';

const App = () => {
  return (
    <Router>
      <Routes>
        <Route path="/" element={<PageHome />} />
        <Route path="/page-shop" element={<PageShop />} />
        <Route path="/page-carts" element={<PageCarts />} />
        <Route path="/page-wishlist" element={<PageWishLisht />} />
        <Route path="/page-checkout" element={<PageCheckout />} />
        <Route path="/page-search" element={<PageSearch />} />
        <Route path="/product-detail/:productId" element={<PageDetailProduct />} />
        <Route path="/page-done" element={<PageDone />} />
      </Routes>
    </Router>
  );
};

export default App;