// Card.js
import React from "react";
import { useNavigate } from "react-router-dom";
import Alert from '@mui/material/Alert';

const Card = ({ product, handleAddToWishlist, handleAddToCart, wishlist, showSuccessAlert }) => {
    // Mengelola Data Product untuk ditampilkan ke detail prodcut
    const navigate = useNavigate();
    const handleProductClick = (productId) => {
        navigate(`/product-detail/${productId}`); // Navigate to detail page with ID
    };

    return (
        <div key={product.id} className="card-product" onClick={() => handleProductClick(product.id)}>
            {/* Card Top */}
            <div className="card-top">
                <div className='description-top'>
                    {product.type[0] === "Top Sales" ? <p className='top-sales'>{product.type[0]}</p> : <></>}
                    {product.type[0] === "Sale" ? <p className='sale'>{product.type[0]}</p> : <></>}
                    {product.type[0] === "Sugar Free" ? <p className='sugar-free'>{product.type[0]}</p> : <></>}
                    <button onClick={(event) => {
                        handleAddToWishlist(product.id);
                        event.stopPropagation();
                    }}>
                        {wishlist.includes(product.id) ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                    </button>
                </div>
                <div className="image">
                    <img src={product.image} alt={product.name} />
                    <img src="assets/image/bg-product.png" alt={product.name} />
                </div>
            </div>

            <h3>{product.name}</h3>
            <p>Weight: {product.weight}g</p>
            <p>${product.price}</p>

            <div className="card-alert">
                {showSuccessAlert && (
                    <Alert variant="filled" severity="success" sx={{ minHeight: 80 }}>
                        Product "{product.name}" added to cart!
                    </Alert>
                )}
            </div>

            {/* Card Bottom */}
            <div className="card-bottom">
                <button type="button" className="btn-a" onClick={(event) => {
                    handleAddToCart(product.id);
                    event.stopPropagation();
                }}>
                    Add to Cart
                </button>
            </div>
        </div>
    );
};

export default Card;
