import React from 'react';
import { useLocation, Link } from "react-router-dom";
import productData from '../data/productsData';
import Navigation from '../components/navigation';
import ProductsPagination from '../components/product-pagination';
import NewsLetter from '../components/news-letter';
import Footer from '../components/footer';

const PageShop = () => {
    // Mengelola State dari Navigation
    const { state } = useLocation();
    const title = state ? state.title : "Best Products";
    return (
        <>
            <Navigation />
            {(title)?(
            <div className="navigation-secondary container">
                <Link to="/"><span>Home</span></Link>
                <i id='chevron' className="fa-solid fa-chevron-right"></i>
                <p>{title}</p>
            </div>
            ) : (
                <p></p>
            )}
            <ProductsPagination productsData={productData} itemsPerPage={20} />
            <NewsLetter />
            <Footer />
        </>
    );
};

export default PageShop;