import * as React from 'react';
import PropTypes from 'prop-types';
import { 
    Box, Stepper, Step, StepLabel, StepContent, Button, Paper, Typography, TextField, Tabs, Tab,
} from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        primary: {
            main: 'rgb(228, 42, 42)', // Merah (sudah terlewati)
        },
    },
});

function CustomTabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}>

            {value === index && (
                <Box sx={{ p: 3 }}>
                    {children}
                </Box>
            )}
        </div>
    );
}

CustomTabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

export default function StepperForm({ setAllStepsCompleted }) {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const [activeStep, setActiveStep] = React.useState(0);
    const [personalDetails, setPersonalDetails] = React.useState({
        fullName: '',
        email: '',
        phoneNumber: ''
    });
    const [deliveryAddress, setDeliveryAddress] = React.useState({
        address: '',
        city: '',
        postalCode: ''
    });
    const [paymentMethod, setPaymentMethod] = React.useState({ payment: '' });

    const handleNext = () => {
        if (activeStep === 0 && !validatePersonalDetails()) {
            return;
        }
        if (activeStep === 1 && !validateDeliveryAddress()) {
            return;
        }
        if (activeStep === 2 && !validatePaymentMethod()) {
            return;
        }

        setActiveStep((prevActiveStep) => {
            const isLastStep = activeStep === 2;
            if (isLastStep) {
                setAllStepsCompleted(true); // Setel state menjadi true jika mencapai langkah terakhir
            }
            return prevActiveStep + 1;
        });
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleReset = () => {
        setActiveStep(0);
        setAllStepsCompleted(false); // Setel state menjadi false jika mereset step
    };

    const validatePersonalDetails = () => {
        return (
            personalDetails.fullName.trim() !== '' &&
            personalDetails.phoneNumber.trim() !== ''
        );
    };

    const validateDeliveryAddress = () => {
        return (
            deliveryAddress.address.trim() !== '' &&
            deliveryAddress.city.trim() !== '' &&
            deliveryAddress.postalCode.trim() !== ''
        );
    };

    const validatePaymentMethod = () => {
        return (
            paymentMethod.payment.trim() !== ''
        );
    }

    return (
        <ThemeProvider theme={theme}>
            <Box sx={{ maxWidth: 350 }}>
                <Stepper activeStep={activeStep} orientation="vertical" color='succes'>
                    {/* Personal Details */}
                    <Step>
                        <StepLabel>
                            Personal Details
                        </StepLabel>
                        <StepContent>
                            <Box>
                                <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
                                    <Tab label="Guest" {...a11yProps(0)} sx={{ textTransform: 'capitalize' }} />
                                    <Tab label="Sing In" {...a11yProps(1)} sx={{ textTransform: 'capitalize' }} />
                                </Tabs>
                            </Box>
                            <CustomTabPanel value={value} index={0}>
                                <Box sx={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    gap: '20px',
                                }}>
                                    <TextField
                                        label="Full Name"
                                        value={personalDetails.fullName}
                                        onChange={(e) => setPersonalDetails({ ...personalDetails, fullName: e.target.value })}
                                        required
                                    />
                                    <TextField
                                        type='number'
                                        label="Phone Number"
                                        value={personalDetails.phoneNumber}
                                        onChange={(e) => setPersonalDetails({ ...personalDetails, phoneNumber: e.target.value })}
                                        required
                                    />
                                    <TextField
                                        type='email'
                                        label="Email (optional)"
                                        value={personalDetails.email}
                                        onChange={(e) => setPersonalDetails({ ...personalDetails, email: e.target.value })}
                                    />
                                </Box>
                                <Box>
                                    <Button
                                        variant="contained"
                                        onClick={handleNext}
                                        sx={{
                                            mt: 1,
                                            mr: 1,
                                            p: '7px 25px',
                                            textTransform: 'capitalize',
                                            borderRadius: '200px',
                                            color: 'rgb(250, 220, 220)',
                                            backgroundColor: 'rgb(228, 42, 42)',
                                            '&:hover': { backgroundColor: 'rgb(158, 31, 31)' },
                                            '& i': { pl: '10px' }
                                        }}>
                                        Next<i id='chevron' className="fa-solid fa-chevron-right"></i>
                                    </Button>
                                </Box>
                            </CustomTabPanel>
                            <CustomTabPanel value={value} index={1}>
                                <Box sx={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    gap: '20px',
                                    mb: 1
                                }}>
                                    {/* Sign In Form */}
                                    <TextField label="Username" />
                                    <TextField label="Password" type="password" />
                                    <Button
                                        variant="contained"
                                        sx={{
                                            mt: 1,
                                            mr: 1,
                                            p: '7px 25px',
                                            textTransform: 'capitalize',
                                            borderRadius: '200px',
                                            color: 'rgb(250, 220, 220)',
                                            backgroundColor: 'rgb(228, 42, 42)',
                                            '&:hover': { backgroundColor: 'rgb(158, 31, 31)' },
                                            '& i': { pl: '10px' }
                                        }}>
                                        Sign In
                                    </Button>
                                </Box>
                                <p style={{ textAlign: 'center' }}>Or</p>
                                <Box sx={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                    gap: '5px',
                                }}>
                                    <Button
                                        variant="contained"
                                        sx={{
                                            mt: 1,
                                            mr: 1,
                                            textTransform: 'capitalize',
                                            borderRadius: '200px',
                                            color: 'black',
                                            background: 'none',
                                            '&:hover': { color: 'rgb(250, 220, 220)', backgroundColor: 'rgb(228, 42, 42)' }
                                        }}>
                                        Sign In with Google
                                    </Button>
                                    <Button
                                        variant="contained"
                                        sx={{
                                            mt: 1,
                                            mr: 1,
                                            textTransform: 'capitalize',
                                            borderRadius: '200px',
                                            color: 'black',
                                            background: 'none',
                                            '&:hover': { color: 'rgb(250, 220, 220)' }
                                        }}>
                                        Sign In with Facebook
                                    </Button>
                                </Box>
                            </CustomTabPanel>
                        </StepContent>
                    </Step>

                    {/* Delivery Address */}
                    <Step>
                        <StepLabel>Delivery Address</StepLabel>
                        <StepContent>
                            <Box sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                gap: '20px',
                            }}>
                                <TextField
                                    label="Address"
                                    multiline
                                    value={deliveryAddress.address}
                                    onChange={(e) => setDeliveryAddress({ ...deliveryAddress, address: e.target.value })}
                                    required
                                />
                                <TextField
                                    label="City"
                                    value={deliveryAddress.city}
                                    onChange={(e) => setDeliveryAddress({ ...deliveryAddress, city: e.target.value })}
                                    required
                                />
                                <TextField
                                    type='number'
                                    label="Postal Code"
                                    value={deliveryAddress.postalCode}
                                    onChange={(e) => setDeliveryAddress({ ...deliveryAddress, postalCode: e.target.value })}
                                    required
                                />
                            </Box>
                            <Box sx={{ mb: 2 }}>
                                <Button
                                    onClick={handleBack}
                                    sx={{
                                        mt: 1,
                                        mr: 1,
                                        textTransform: 'capitalize',
                                        color: 'rgb(228, 42, 42)'
                                    }}>
                                    Back
                                </Button>
                                <Button
                                    variant="contained"
                                    onClick={handleNext}
                                    sx={{
                                        mt: 1,
                                        mr: 1,
                                        p: '7px 25px',
                                        textTransform: 'capitalize',
                                        borderRadius: '200px',
                                        color: 'rgb(250, 220, 220)',
                                        backgroundColor: 'rgb(228, 42, 42)',
                                        '&:hover': { backgroundColor: 'rgb(158, 31, 31)' },
                                        '& i': { pl: '10px' }
                                    }}>
                                    Next<i id='chevron' className="fa-solid fa-chevron-right"></i>
                                </Button>
                            </Box>
                        </StepContent>
                    </Step>

                    {/* Payment Method */}
                    <Step>
                        <StepLabel optional={<Typography variant="caption">Last step</Typography>}>Payment Method</StepLabel>
                        <StepContent>
                            <Box sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                gap: '5px',
                            }}>
                                <TextField
                                    label="Payment Method"
                                    value={paymentMethod.payment}
                                    onChange={(e) => setPaymentMethod({ ...paymentMethod, payment: e.target.value })}
                                    required
                                />
                            </Box>
                            <Box sx={{ mb: 2 }}>
                                <Button
                                    onClick={handleBack}
                                    sx={{
                                        mt: 1,
                                        mr: 1,
                                        textTransform: 'capitalize',
                                        color: 'rgb(228, 42, 42)'
                                    }}>
                                    Back
                                </Button>
                                <Button
                                    variant="contained"
                                    onClick={handleNext}
                                    sx={{
                                        mt: 1,
                                        mr: 1,
                                        p: '7px 25px',
                                        textTransform: 'capitalize',
                                        borderRadius: '200px',
                                        color: 'rgb(250, 220, 220)',
                                        backgroundColor: 'rgb(228, 42, 42)',
                                        '&:hover': { backgroundColor: 'rgb(158, 31, 31)' },
                                        '& i': { pl: '10px' }
                                    }}>
                                    Finish<i id='chevron' className="fa-solid fa-chevron-right"></i>
                                </Button>
                            </Box>
                        </StepContent>
                    </Step>

                </Stepper>
                {activeStep === 3 && (
                    <Paper square elevation={0} sx={{ p: 3 }}>
                        <Typography>All steps completed - you&apos;re finished, please place the order</Typography>
                        <Button
                            variant="contained"
                            onClick={handleReset}
                            sx={{
                                mt: 1,
                                mr: 1,
                                p: '7px 25px',
                                textTransform: 'capitalize',
                                borderRadius: '200px',
                                color: 'rgb(250, 220, 220)',
                                backgroundColor: 'rgb(228, 42, 42)',
                                '&:hover': { backgroundColor: 'rgb(158, 31, 31)' },
                                '& i': { pl: '10px' }
                            }}>
                            Reset<i id='chevron' className="fa-solid fa-chevron-right"></i>
                        </Button>
                    </Paper>
                )}
            </Box>
        </ThemeProvider>
    );
}