import * as React from "react";
import { Link, useParams } from "react-router-dom";
import { useCartAndWishlist } from '../controllers/cartAndWishlist';
import Footer from "../components/footer";
import Navigation from "../components/navigation";
import productsData from "../data/productsData";
import Alert from '@mui/material/Alert';

const PageDetailProduct = () => {
    const [product, setProduct] = React.useState(null); // State to store product details
    const { productId } = useParams(); // Get product ID from URL using useParams
    React.useEffect(() => {
        const selectedProduct = productsData.find((item) => item.id === parseInt(productId)); // Find product by ID
        setProduct(selectedProduct);
    }, [productId]); // Run effect only when productId changes

    const [showSuccessAlert, setShowSuccessAlert] = React.useState(false);

    // Menyimpan Data Shopping Carts dan Wishlist Carts Products
    const { cartProducts, setCartProducts, wishlist, setWishlist } = useCartAndWishlist();
    // Menerima ID produk dan menemukan produk cart yang sesuai, kemudian menyimpannya dalam state shopping carts.
    const handleAddToCart = (productId) => {
        const chosenProduct = productsData.find((product) => product.id === productId);

        // Periksa apakah produk sudah ada di keranjang belanja
        const isProductInCart = cartProducts.some((product) => product.id === chosenProduct.id);

        if (isProductInCart) {
            // Jika produk sudah ada di keranjang belanja, update jumlah produk
            const updatedCartProducts = cartProducts.map((product) => {
                if (product.id === chosenProduct.id) {
                    return { ...product, quantity: product.quantity + 1 };
                }
                return product;
            });
            setCartProducts(updatedCartProducts);
            localStorage.setItem("carts", JSON.stringify(updatedCartProducts));
        } else {
            // Jika produk belum ada di keranjang belanja, tambahkan produk baru
            const updatedCartProducts = [...cartProducts, { ...chosenProduct, quantity: 1 }];
            setCartProducts(updatedCartProducts);
            localStorage.setItem("carts", JSON.stringify(updatedCartProducts));
        }

        // Menampilkan pesan ke pengguna
        // alert(`Product "${chosenProduct.name}" added to cart!`);
        // {
        //     <Alert variant="filled" severity="success">
        //         This is a filled success Alert.
        //     </Alert>
        // }


        // Update state to show alert
        setShowSuccessAlert(true);

        // ... (Optional) Delay to simulate alert duration
        setTimeout(() => setShowSuccessAlert(false), 3000); // Hide after 3 seconds
    };
    // Menerima ID produk dan menemukan produk wishlist yang sesuai, kemudian menyimpannya dalam state wishlist carts.
    const handleAddToWishlist = (productId) => {
        if (!wishlist.includes(productId)) {
            setWishlist([...wishlist, productId]);
            localStorage.setItem('wishlist', JSON.stringify([...wishlist, productId]));
        } else {
            const updatedWishlist = wishlist.filter((id) => id !== productId);
            setWishlist(updatedWishlist);
            localStorage.setItem('wishlist', JSON.stringify(updatedWishlist));
            // Set ikon tombol ke regular heart
        }
    };

    return (
        <>
            {/* Content untuk menampilkan Navigation */}
            <Navigation />

            {/* Content untuk menampilkan Detail Product */}
            {product ? (
                <section className="product-detail container">
                    <div className="navigation-secondary">
                        <Link to="/"><span>Home</span></Link>
                        <i id='chevron' className="fa-solid fa-chevron-right"></i>
                        <Link to="/page-shop" state={{ title: product.type[1] }}>{product.type[1]}</Link>
                        <i id='chevron' className="fa-solid fa-chevron-right"></i>
                        <p>{product.name}</p>
                    </div>
                    <h1>{product.name}</h1>
                    <div className="product-detail-content">
                        <div className="image">
                            <img src="assets/image/bg-product.png" alt={product.name} />
                            <img src={product.image} alt={product.name} />
                        </div>
                        <div className="description">
                            <table className="table-group">
                                <tbody>
                                    <tr>
                                        <td><p>Ingredients: </p></td>
                                        <td><p>Sugar, Marzipan Paste, Chocolate, Couverture, Orange Natural Aroma, Powdered Milk, Cocoa Mass, Powdered Whey, Milk Fat, Emulsifer Soya Lecithin, Salt, Flavor Vanilin</p></td>
                                    </tr>
                                    <tr>
                                        <td><p>Nutritional Value: </p></td>
                                        <td><p>Fats - 32.9g, of which Saturated - 10.8g: Carbohydrates - 42.5g, of which Sugar - 41.3g: Proteins - 9.4g: Salt - 0.01g.</p></td>
                                    </tr>
                                    <tr>
                                        <td><p>Energy Value: </p></td>
                                        <td><p>2108 kj/504 kcal</p></td>
                                    </tr>
                                    <tr>
                                        <td><p>Net Weight: </p></td>
                                        <td><p>{product.weight}g</p></td>
                                    </tr>
                                    <tr>
                                        <td><p>Shelf Life: </p></td>
                                        <td><p>6 Months</p></td>
                                    </tr>
                                    <tr>
                                        <td><p>Price: </p></td>
                                        <td><p>${product.price}</p></td>
                                    </tr>
                                </tbody>
                            </table>
                            <div className="button">
                                <button type="button" className="btn-a" onClick={() => handleAddToCart(product.id)}>
                                    Add to Cart
                                </button>
                                <button onClick={() => handleAddToWishlist(product.id)}>
                                    {wishlist.includes(product.id) ? <i className="fa-solid fa-heart"></i>
                                        :
                                        <i className="fa-regular fa-heart"></i>}
                                </button>
                            </div>
                            {showSuccessAlert && (
                                <Alert variant="filled" severity="success">
                                    Product "{product.name}" added to cart!
                                </Alert>
                            )}
                        </div>
                    </div>
                </section>
            ) : (
                <p>Product not found.</p>
            )}

            {/* Content untuk menampilkan Footer */}
            <Footer />
        </>
    );
};

export default PageDetailProduct;
