import React from "react";
import { Link, useNavigate } from 'react-router-dom';
import { useCartAndWishlist } from "../controllers/cartAndWishlist";
import {
    Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle
} from '@mui/material';
import Navigation from "../components/navigation";
import { createTheme, ThemeProvider } from '@mui/material/styles';

const theme = createTheme({
    palette: {
        primary: {
            main: 'rgb(228, 42, 42)', // Merah (sudah terlewati)
        },
    },
});

const PageCarts = () => {
    const [open, setOpen] = React.useState(false);
    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    // State untuk menyimpan produk yang ada di keranjang belanja
    const { cartProducts, setCartProducts } = useCartAndWishlist();

    // Fungsi untuk menghapus produk dari keranjang belanja
    const handleRemoveFromCart = (productId) => {
        const updatedCartProducts = cartProducts.filter(product => product.id !== productId);
        setCartProducts(updatedCartProducts);
        localStorage.setItem("carts", JSON.stringify(updatedCartProducts));
    };

    // Fungsi untuk menambah jumlah produk
    const handleIncrementQuantity = (productId) => {
        const updatedCartProducts = cartProducts.map(product => {
            if (product.id === productId) {
                return { ...product, quantity: product.quantity + 1 };
            }
            return product;
        });
        setCartProducts(updatedCartProducts);
        localStorage.setItem("carts", JSON.stringify(updatedCartProducts));
    };

    // Fungsi untuk mengurangi jumlah produk
    const handleDecrementQuantity = (productId) => {
        const updatedCartProducts = cartProducts.map(product => {
            if (product.id === productId && product.quantity > 1) {
                return { ...product, quantity: product.quantity - 1 };
            }
            return product;
        });
        setCartProducts(updatedCartProducts);
        localStorage.setItem("carts", JSON.stringify(updatedCartProducts));
    };

    // Fungsi untuk mendapatkan objek history
    const history = useNavigate();

    // Menampilkan pesan ke pengguna
    const handleCheckout = () => {
        // Tampilkan alert
        history('/page-checkout');
    };

    // Menghitung total harga
    const totalPrice = cartProducts.reduce((total, product) => {
        return total + (product.price * product.quantity);
    }, 0);

    return (
        <>
            {/* Content untuk menampilkan halaman carts */}
            <Navigation />
            <div className="navigation-secondary container">
                <Link to="/"><span>Home</span></Link>
                <i id='chevron' className="fa-solid fa-chevron-right"></i>
                <p>Shopping Carts</p>
            </div>
            <section className="shopping-cart">
                <h1>Shopping Carts</h1>
                <div className="overflow-cart">
                    <table className="table-group">
                        <thead>
                            <tr className="table-title">
                                <th>Product</th>
                                <th>Price</th>
                                <th>Quantity</th>
                                <th>Total</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td><div className="line"></div></td>
                            </tr>
                        </tbody>
                        <tbody>
                            {cartProducts.length > 0 ? (
                                cartProducts.map((product) => (
                                    <tr key={product.id} className="table-item">
                                        <td className="product-content">
                                            <img src={product.image} alt={product.name} />
                                            <div className="product-description">
                                                <h3>{product.name}</h3>
                                                <p>Weight: {product.weight}g</p>
                                            </div>
                                        </td>
                                        <td><strong>${product.price}</strong></td>
                                        <td className="pagination">
                                            <button type="button" onClick={() => handleDecrementQuantity(product.id)}>
                                                <i className="fa-solid fa-minus"></i>
                                            </button>
                                            {product.quantity}
                                            <button type="button" onClick={() => handleIncrementQuantity(product.id)}>
                                                <i className="fa-solid fa-plus"></i>
                                            </button>
                                        </td>
                                        <td><strong>${(product.price * product.quantity).toLocaleString('en-US')}</strong></td>
                                        <td>
                                            <button type="button" onClick={() => handleRemoveFromCart(product.id)}>
                                                <i className="fa-solid fa-xmark"></i>
                                            </button>
                                        </td>
                                    </tr>
                                ))
                            ) : (
                                <tr>
                                    <td>
                                        <p><strong>Empty shopping cart!</strong></p>
                                        <Link to="/page-shop" state={{ title: "All Products" }}>
                                            <strong><i className="fa-solid fa-chevron-left"></i> Shop Now</strong></Link>
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                </div>
                <div className="content">
                    <Link to="/page-shop" state={{ title: "Gift Sweets" }}>
                        <i className="fa-solid fa-gift"></i> Add a Gift Box</Link>
                    <p><strong>Total to Pay: ${totalPrice.toLocaleString('en-US')}</strong></p>
                </div>
                <div className="navigation">
                    <Link to="/page-shop" state={{ title: "All Products" }}>
                        <i className="fa-solid fa-chevron-left"></i> Continue Shopping</Link>
                    <button className="btn-a" onClick={totalPrice === 0 ? handleClickOpen : handleCheckout}>Checkout</button>
                </div>
            </section>
            <ThemeProvider theme={theme}>
                <React.Fragment>
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                    >
                        <DialogTitle id="alert-dialog-title">
                            {"You are wrong?"}
                        </DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                {totalPrice === 0 ? (
                                    "Shopping cart is empty, Shop Now!"
                                ) : (
                                    "Total Shopping: " + totalPrice.toLocaleString('en-US')
                                )}
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleClose}>OK</Button>
                        </DialogActions>
                    </Dialog>
                </React.Fragment>
            </ThemeProvider>
        </>
    );
};

export default PageCarts;
