import * as React from 'react';
import { Link } from 'react-router-dom';
import AnimatedComponent from '../controllers/AnimatedComponent';
import Footer from '../components/footer';
import productData from '../data/productsData';
import NewsLetter from '../components/news-letter';
import Navigation from '../components/navigation';
import ProductsPagination from '../components/product-pagination';

const PageHome = () => {
    const [isLoading, setIsLoading] = React.useState(true);

    React.useEffect(() => {
        // Set timeout untuk simulasi pemuatan
        const timeout = setTimeout(() => {
            setIsLoading(false);
        }, 5000);

        return () => clearTimeout(timeout);
    }, []);

    return (
        <>
            {isLoading ? (
                // Tampilkan animasi loading jika masih memuat
                <div id="loading-screen">
                    <div className="wrapper">
                        <div className="circle"></div>
                        <div className="circle"></div>
                        <div className="circle"></div>
                        <div className="shadow"></div>
                        <div className="shadow"></div>
                        <div className="shadow"></div>
                    </div>
                </div>
            ) : (
                <div>
                    {/* Navigation */}
                    <Navigation />

                    {/* Header */}
                    <header>
                        <div className="header-grid content">
                            <div className="description">
                                <p>Large Collection of</p>
                                <h2>Sweet and Candy</h2>
                                <p>Choosen from over 100 different sweets to create your own, unique selection of personal favorites!</p>
                            </div>
                            <div className="hero">
                                <img src="assets/image/hero.png" alt="Candy" />
                                <img src="assets/image/bg-hero.png" alt="Candy" />
                            </div>
                            <div className="button">
                                <Link to="/page-shop" state={{ title: "All Products" }} className='btn-a'>Shop Now</Link>
                            </div>
                        </div>
                    </header>

                    {/* Categories */}
                    <AnimatedComponent animationType="fade-in">
                        <section className="categories container">
                            <h2>Our Categories</h2>
                            <div className="category-cards">
                                <div className="card">
                                    <div className="card-body">
                                        <h2 className="card-title">Candy</h2>
                                    </div>
                                    <img src="assets/image/categories/candy.png" className="card-img-top" alt="Title" />
                                </div>

                                <div className="card">
                                    <div className="card-body">
                                        <h2 className="card-title">Chocolate Bar</h2>
                                    </div>
                                    <img
                                        className="card-img-top"
                                        src="assets/image/categories/chocolate-bar.png"
                                        alt="Title"
                                    />
                                </div>

                                <div className="card">
                                    <div className="card-body">
                                        <h2 className="card-title">Diet Sweets</h2>
                                    </div>
                                    <img
                                        className="card-img-top"
                                        src="assets/image/categories/biscuit-chocolate.png"
                                        alt="Title"
                                    />
                                </div>

                                <div className="card">
                                    <div className="card-body">
                                        <h2 className="card-title">Gift Sweets</h2>
                                    </div>
                                    <img
                                        className="card-img-top"
                                        src="assets/image/categories/gift-chocolate.png"
                                        alt="Title"
                                    />
                                </div>
                            </div>
                        </section>
                    </AnimatedComponent>

                    {/* Products */}
                    <main>
                        <AnimatedComponent animationType="slide-in">
                            <ProductsPagination productsData={productData} itemsPerPage={4} />
                        </AnimatedComponent>
                    </main>

                    {/* About Us */}
                    <main id='about'>
                        <AnimatedComponent animationType="fade-in">
                            <section className="about container">
                                <div className="content">
                                    <div className="description">
                                        <h2>About Us</h2>
                                        <p>Sweets Candy it an online sweet shop with a large collection of candies and chocolate. Our sweets are made from only the best natural ingredients. Here you can find a special gift chocolate for any event or celebration. There is also a line of healthy sweets without sugar, nuts, dairy products</p>
                                        <Link to="/page-shop" state={{ title: "All Products" }} className='btn-a'>Go to Shop <i className="fa-solid fa-angle-right"></i></Link>
                                    </div>
                                    <div className='image'>
                                        <img src="assets/image/bg-about.png" alt="img" />
                                        <img src="assets/image/about.png" alt="img" />
                                    </div>
                                </div>
                            </section>
                        </AnimatedComponent>
                    </main>

                    {/* NewsLetter */}
                    <AnimatedComponent animationType="fade-in">
                        <NewsLetter />
                    </AnimatedComponent>

                    {/* Footer */}
                    <Footer />
                </div>
            )}
        </>
    );
};

export default PageHome;
