import * as React from 'react';
import { Link, useNavigate } from 'react-router-dom';

function Navigation() {
    const navigate = useNavigate();

    const [searchTerm, setSearchTerm] = React.useState(''); // State untuk menyimpan query pencarian

    const handleSearchSubmit = (event) => {
        event.preventDefault();
        navigate('/page-search', { state: { searchTerm } }); // Pass search term as state
    };

    const [isOpen, setIsOpen] = React.useState(window.innerWidth > 701 ? true : false); // State to control menu visibility
    const toggleMenu = () => {
        setIsOpen(!isOpen); // Toggle state isOpen on every button click
    };

    // Menambahkan useEffect untuk menangani state menu berdasarkan ukuran layar
    React.useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth < 701) {
                setIsOpen(false); // Tutup menu saat resize di atas 1000px
            } else {
                setIsOpen(true);
            }
        };
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    return (
        <nav>
            {/* Navigation */}
            <div className="nav-grid container">
                <button className="button-menu disable" onClick={toggleMenu}>
                    <i className="fa-solid fa-bars"></i>
                </button>
                <div className="logo">
                    <Link to="/">Sweets Candy</Link>
                </div>
                <div className="search">
                    <form className="form-search" action="/search" method="GET" onSubmit={handleSearchSubmit}>
                        <input type="search" placeholder="Search..." value={searchTerm} onChange={(event) => setSearchTerm(event.target.value)} />
                        <button type="submit">
                            <i className="fa-solid fa-magnifying-glass"></i>
                        </button>
                    </form>
                </div>
                <div className="icon">
                    <ul>
                        <li>
                            <Link to="/page-wishlist">
                                <i className="fa-regular fa-heart"></i>
                            </Link>
                        </li>
                        <li>
                            <Link to="/page-carts">
                                <i className="fa-solid fa-cart-shopping"></i>
                            </Link>
                        </li>
                        <li>
                            <Link to="/">
                                <i className="fa-regular fa-user"></i>
                            </Link>
                        </li>
                    </ul>
                </div>
            </div>
            {/* Menu List */}
            {isOpen && ( // Conditionally render menu list only if isOpen is true
                <div className="menu">
                    <ul>
                        <button className='disable' onClick={toggleMenu}><i className="fa-solid fa-xmark"></i></button>
                        <li className='disable'>
                            <Link to="/"><i className="fa-regular fa-user"></i> Account</Link>
                        </li>
                        <li>
                            <Link to="/page-shop" state={{ title: "Candy" }}>Candy</Link>
                        </li>
                        <li>
                            <Link to="/page-shop" state={{ title: "Chocolate Bars" }}>Chocolate Bars</Link>
                        </li>
                        <li>
                            <Link to="/page-shop" state={{ title: "Diet Sweets" }}>Diet Sweets</Link>
                        </li>
                        <li>
                            <Link to="/page-shop" state={{ title: "Gift Sweets" }}>Gift Sweets</Link>
                        </li>
                        <li>
                            <Link to="/">About Us</Link>
                        </li>
                    </ul>
                </div>
            )}
        </nav>
    );
}

export default Navigation;
