import * as React from 'react';
import { Link } from 'react-router-dom';
import listData from '../data/listData';

const Footer = () => {
    const [openItem, setOpenItem] = React.useState(window.innerWidth > 700);
    
    // Menambahkan useEffect untuk menangani state menu berdasarkan ukuran layar
    React.useEffect(() => {
        const handleResize = () => {
            setOpenItem(window.innerWidth > 700);
        };
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);

    const collapse = (itemId) => {
        if (window.innerWidth > 700) return; // Jika layar lebih dari 700 piksel, jangan izinkan pembukaan/tutupan item
        setOpenItem(itemId === openItem ? null : itemId);
    };

    return (
        <>
            <footer className="container">
                <div className="footer-top">
                    <Link to="/" className="logo">Sweets Candy</Link>
                    {listData.map((item) => (
                        <div key={item.id} className="footer-item" onClick={() => collapse(item.id)}>
                            <p>
                                {item.title}
                                {(window.innerWidth > 700 ) ? (
                                    <i></i> // Chevron Down when open
                                ) : (
                                    (openItem === item.id) ? (
                                        <i id='chevron' className="fa-solid fa-chevron-down"></i> // Chevron Down when open
                                    ) : (
                                        <i id='chevron' className="fa-solid fa-chevron-right"></i> // Chevron Right when closed
                                    )
                                )}
                            </p>
                            {(openItem === item.id || window.innerWidth > 700) && (
                                <div className="footer-content">
                                    {item.content}
                                </div>
                            )}
                        </div>
                    ))}
                </div>
                <div className="line"></div>
                <div className="footer-bottom">
                    <p>Copyright  Online Store "Sweets Candy" 2024.</p>
                </div>
            </footer>
        </>
    );
};

export default Footer;
