import React from "react";

function NewsLetter() {
    return (
        <main className="container">
            <section className="news-letter">
                <div className="description">
                    <h2>Get Our Newsletter</h2>
                    <p>Subscribe to our newsletter and know latest promotions and news!</p>
                </div>
                <form className="form-newsletter" action="/newsletter" method="GET">
                    <input className="input-letter" type="email" placeholder="email@gmail.com" required/>
                    <button type="submit" className="btn-a">Subscribe</button>
                </form>
            </section>
        </main>
    )
}

export default NewsLetter;