import React from 'react';
import { useNavigate } from 'react-router-dom';

export default function Error() {
    // Fungsi untuk mendapatkan objek history
    const history = useNavigate();
    // Menampilkan pesan ke pengguna
    const handleBackToShop = () => {
        history('/');
    };

    return (
        <>
            <div className="done-error">
                <div className="image">
                    <img src="assets/image/bg-hero.png" alt="error" />
                </div>
                <div className="message">
                    <div className="icon">
                        <i className="fa-solid fa-times"></i> {/* Use a different icon for error */}
                    </div>
                    <h2>You are wrong?</h2>
                    <h2>ERROR: 404</h2>
                    <p>An error occurred while processing this page. Please try again.</p>
                </div>
            </div>
            <div className="button" onClick={handleBackToShop}>
                <button className="btn-a">Back to Home</button>
            </div>
        </>
    )
}